import { useTitle } from 'react-use';
import { Link } from 'react-router-dom';
import { useEffect, useState } from 'react';
// Ant Design Resources
import { Col, Divider, Layout, Row, Select } from 'antd';
// Hooks
import { useQueryParams } from 'hooks/useQueryParams';
// Utils
import { AVAILABLE_AVATAR_IDS, AVATARS } from 'utils/avatars';
import { makeArray } from 'utils/helpers';
// Components
import { Avatar } from 'components/avatars';
import { SheepAvatar } from 'games/mente-coletiva/components/SheepAvatar';
import { CostumeAvatar } from 'components/avatars/CostumeAvatar';
import { ClubberAvatar } from 'components/avatars/ClubberAvatar';
import { GlyphCard } from 'components/cards/GlyphCard';
import { ItemCard } from 'components/cards/ItemCard';
import { DevHeader } from './DevHeader';
import { MEDALS_IDS } from './utils/constants';
import { Medal } from 'components/general/Medal';
import { TreeCard } from 'components/cards/TreeCard';
import { SuperHeroAvatar } from 'components/avatars/SuperHeroAvatar';
import { EmojiCard } from 'components/cards/EmojiCard';
import { orderBy } from 'lodash';
import { SignCard } from 'components/cards/SignCard';
import { TOTAL_ALIEN_ITEMS } from 'utils/constants';

type SpriteOption = {
  key: string;
  label: string;
  prefix: string;
  quantity: number;
  extra?: string[];
  startAt: number;
};

const options: Record<string, SpriteOption> = {
  avatars: {
    key: 'avatars',
    label: 'Avatars',
    prefix: 'avatar',
    quantity: 50,
    extra: ['A', 'B', 'C', 'D', 'E', 'N'],
    startAt: 0,
  },
  'alien-signs': {
    key: 'alien-signs',
    label: 'Alien Signs',
    prefix: 'sign',
    quantity: 36,
    startAt: 0,
  },
  sheep: {
    key: 'sheep',
    label: 'Sheep',
    prefix: 'sheep-face',
    quantity: 25,
    startAt: 0,
  },
  costumes: {
    key: 'costumes',
    label: 'Costumes',
    prefix: 'costume',
    quantity: 50,
    startAt: 0,
  },
  clubbers: {
    key: 'clubbers',
    label: 'Clubbers',
    prefix: 'clubber',
    quantity: 60,
    startAt: 0,
  },
  'super-heroes': {
    key: 'super-heroes',
    label: 'Super Heroes',
    prefix: 'super-hero',
    quantity: 50,
    startAt: 0,
  },
  glyphs: {
    key: 'glyphs',
    label: 'Glyphs',
    prefix: 'glyph',
    quantity: Number(TOTAL_ALIEN_ITEMS),
    startAt: 1,
  },
  items: {
    key: 'items',
    label: 'Items',
    prefix: 'item',
    quantity: 946,
    startAt: 1,
  },
  trees: {
    key: 'trees',
    label: 'Trees',
    prefix: 'tree',
    quantity: 15,
    startAt: 1,
  },
  emojis: {
    key: 'emojis',
    label: 'Emojis',
    prefix: 'emoji',
    quantity: 30,
    startAt: 1,
  },
  medals: {
    key: 'medals',
    label: 'Medals',
    prefix: 'medal',
    quantity: 100,
    startAt: 1,
  },
};

const optionsList = orderBy(Object.values(options), ['label'], ['asc']);

function SpritesPage() {
  const [active, setActive] = useState(options.avatars);
  useTitle(`${active.label} Sprites | Dev | Tarde Divertida`);
  const qp = useQueryParams({ active: 'avatars' });

  useEffect(() => {
    setActive(options[qp.queryParams.active] ?? options.avatars);
  }, [qp.queryParams.active]);

  const activeContent = {
    avatars: <AvatarsContent />,
    glyphs: <GlyphsContent />,
    items: <ItemsContent />,
    medals: <MedalsContent />,
    trees: <TreeContent />,
    emojis: <EmojisContent />,
    'alien-signs': <AlienSignsContent />,
  }?.[active.key] ?? <Content type={active.key} />;

  return (
    <Layout className="dev-layout">
      <DevHeader
        title={
          <Select
            onChange={(e) => qp.add('active', e)}
            value={qp.queryParams.active}
            size="small"
            style={{ minWidth: '15ch' }}
          >
            {optionsList.map((option) => (
              <Select.Option value={option.key} key={option.key}>
                {option.label}
              </Select.Option>
            ))}
          </Select>
        }
        subTitle={
          <>
            ({active.quantity}){active.key === 'items' && <Link to="/dev/classifier"> Classifier</Link>}
          </>
        }
      />
      <Layout.Content className="dev-content">{activeContent}</Layout.Content>
    </Layout>
  );
}

function AvatarsContent() {
  const AI_AVATARS = Object.keys(AVATARS).filter((id) => !AVAILABLE_AVATAR_IDS.includes(id));

  return (
    <>
      <ul className="sprites__grid-5">
        {AVAILABLE_AVATAR_IDS.map((avatarId) => {
          const avatar = AVATARS[avatarId];
          return (
            <li
              key={avatar.id}
              className="sprites__avatar-grid-item"
              style={{ backgroundColor: avatar.color }}
            >
              <div style={{ overflow: 'hidden', textAlign: 'center' }}>[{avatar.id}]</div>
              <Avatar id={avatar.id} size={64} />
              <div style={{ overflow: 'hidden', textAlign: 'center' }}>
                <p>
                  {avatar.description.en}
                  <br />
                  {avatar.description.pt}
                </p>
              </div>
            </li>
          );
        })}
      </ul>
      <Divider />
      <ul className="sprites__grid-5">
        {AI_AVATARS.map((avatarId) => {
          const avatar = AVATARS[avatarId];
          return (
            <li
              key={avatar.id}
              className="sprites__avatar-grid-item"
              style={{ backgroundColor: avatar.color }}
            >
              <Avatar id={avatar.id} size={64} />
              <div style={{ overflow: 'hidden', textAlign: 'center' }}>
                <p>
                  {avatar.description.en}
                  <br />
                  {avatar.description.pt}
                </p>
              </div>
            </li>
          );
        })}
      </ul>
    </>
  );
}

function Content({ type }: { type: string }) {
  const { quantity, startAt } = options[type];

  const ids = makeArray(quantity, startAt);

  return (
    <ul className="sprites__flex">
      {ids.map((id) => {
        return (
          <li key={`content-${id}`} className="sprites__flex-item">
            {type === 'sheep' && <SheepAvatar sheepId={String(id)} id="A" />}
            {type === 'costumes' && <CostumeAvatar id={String(id)} avatarId="A" />}
            {type === 'clubbers' && <ClubberAvatar id={String(id)} avatarId="A" />}
            {type === 'super-heroes' && <SuperHeroAvatar id={String(id)} avatarId="A" />}
          </li>
        );
      })}
    </ul>
  );
}

function ItemsContent() {
  const { quantity, startAt } = options.items;

  const ids = makeArray(quantity, startAt);

  return (
    <ul className="sprites__flex">
      {ids.map((id) => {
        return (
          <li key={`items-${id}`} className="sprites__flex-item">
            <ItemCard id={String(id)} />
            {id}
          </li>
        );
      })}
    </ul>
  );
}

function AlienSignsContent() {
  const { quantity, startAt } = options['alien-signs'];

  const ids = makeArray(quantity, startAt);

  return (
    <ul className="sprites__flex">
      {ids.map((id) => {
        return (
          <li key={`items-${id}`} className="sprites__flex-item">
            <SignCard id={String(id)} />
            {id}
          </li>
        );
      })}
    </ul>
  );
}

function EmojisContent() {
  const { quantity, startAt } = options.emojis;

  const ids = makeArray(quantity, startAt);

  return (
    <ul className="sprites__flex">
      {ids.map((id) => {
        return (
          <li key={`items-${id}`} className="sprites__flex-item">
            <EmojiCard id={String(id)} />
            {id}
          </li>
        );
      })}
    </ul>
  );
}

function GlyphsContent() {
  const { quantity, startAt } = options.glyphs;

  const ids = makeArray(quantity, startAt);

  return (
    <ul className="sprites__flex">
      {ids.map((id) => {
        return (
          <li key={`costume-${id}`} className="sprites__flex-item">
            <GlyphCard id={String(id)} />
            {id}
          </li>
        );
      })}
    </ul>
  );
}

function MedalsContent() {
  return (
    <Row gutter={8}>
      {MEDALS_IDS.map((id) => {
        return (
          <Col xs={6} sm={6} md={4} lg={4} xl={2} key={`medal-${id}`} className="sprites__col">
            <Medal id={String(id)} width={100} />
            {id}
          </Col>
        );
      })}
    </Row>
  );
}

function TreeContent() {
  const { quantity, startAt } = options.trees;

  const ids = makeArray(quantity, startAt);

  return (
    <ul className="sprites__flex">
      {ids.map((id) => {
        return (
          <li key={`tree-${id}`} className="sprites__flex-item">
            <TreeCard id={String(id)} />
            {id}
          </li>
        );
      })}
    </ul>
  );
}

export default SpritesPage;
